<template>
  <div class="app">
    <el-table :data="tableData" style="width: 100%" height="540"
    border>
      <el-table-column prop="title" label="标题" width="650"></el-table-column>
      <el-table-column prop="url" label="地址" width="650"></el-table-column>
      <el-table-column prop="gmtCreate" label="日期"></el-table-column>
    </el-table>
    <div class="tabListPage">
      <el-pagination
        background
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-sizes="pageSizes"
        :page-size="PageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="100"
      ></el-pagination>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      // 总数据
      tableData: [],
      // 默认显示第几页
      currentPage: 1,
      // 总条数，根据接口获取数据长度(注意：这里不能为空)
      totalCount: 1000,
      // 个数选择器（可修改）
      pageSizes: [10, 20, 50, 100],
      // 默认每页显示的条数（可修改）
      PageSize: 10
    }
  },
  methods: {
    // 将页码，及每页显示的条数以参数传递提交给后台y
    getData (n1, n2) {
      // 这里使用axios，使用时请提前引入
      this.axios
        .post(
          '/rgweb/webapi/getItNews',
          {
            // 每页显示的条数
            pageSize: n1,
            // 显示第几页
            pageIndex: n2
          },
          { emulateJSON: true },
          {
            headers: { 'Content-Type': 'application/json;charset=utf-8' }
          }
        )
        .then(reponse => {
          console.log(reponse)
          // 将数据赋值给tableData
          this.tableData = reponse.data.data.itNewsBeanList
          // 将数据的长度赋值给totalCount
          this.totalCount = reponse.data.data.total
        })
    },
    // 分页
    // 每页显示的条数
    handleSizeChange (val) {
      // 改变每页显示的条数
      this.PageSize = val
      // 点击每页显示的条数时，显示第一页
      this.getData(val, 1)
      // 注意：在改变每页显示的条数时，要将页码显示到第一页
      this.currentPage = 1
    },
    // 显示第几页
    handleCurrentChange (val) {
      debugger
      // 改变默认的页数
      this.currentPage = val
      // 切换页码时，要获取每页显示的条数
      this.getData(this.PageSize, val)
    }
  },
  created: function () {
    this.getData(this.PageSize, this.currentPage)
  }
}
</script>



