<template>
  <div>
    <el-row>
      <el-col :span="12">
        <div class="grid-content bg-purple" style="margin: 70px 20px 20px 20px">
          <el-input
            :rows="40"
            type="textarea"
            placeholder="请输入待格式化内容"
            v-model="textareaOld"
          >
          </el-input>
        </div>
      </el-col>
      <el-col :span="12"
        ><div class="grid-content bg-purple-light">
          <div style="float:right;margin-right:30px;margin-top:10px;">
          <el-select v-model="value" placeholder="展开层级">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
          <el-button type="primary" disabled>格式化</el-button>
          </div>
          <el-input
            :rows="40"
            type="textarea"
            placeholder="格式化内容"
            v-model="textareaNew"
            style="margin: 20px"
          >
          </el-input></div
      ></el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  data() {
    return {
      textareaOld: "",
      textareaNew: "",
      options: [
        {
          value: "100",
          label: "展开",
        },
        {
          value: "-1",
          label: "收起",
        },
        {
          value: "2",
          label: "2级",
        },
        {
          value: "3",
          label: "2级",
        },
        {
          value: "4",
          label: "4级",
        },
        {
          value: "5",
          label: "5级",
        },
        {
          value: "7",
          label: "7级",
        },
        {
          value: "8",
          label: "8级",
        },
      ],
      value: "",
    };
  },
};
</script>


